body {
  margin: 0;
  font-family: "Manrope", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #000;
  font-weight: 400;
}

.project-title {
  font-size: 40px;
  font-weight: 600;
  line-height: 60px;
}

.project-sub-title {
  color: #212529;
  font-size: 40px;
  font-weight: 400;
  line-height: 46px;
}

.project-content {
  color: #545454;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.section-title {
  font-size: 16px;
  font-weight: 700;
  text-transform: capitalize;
}

.keyword-in-title {
  font-size: 35px;
  font-style: italic;
  line-height: 48px;
}

.topic-title {
  font-size: 40px;
  line-height: 48px;
}

.topic-desc {
  color: #545454;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.info-title {
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

.info-content {
  color: #212529;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.dark-section-no {
  font-size: 32px;
  font-weight: 700;
  line-height: 32px;
  letter-spacing: 1.6px;
  text-transform: uppercase;
}

.dark-section-title {
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
}

.dark-section-summary {
  color: #a0a0a0;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
}

.button {
  cursor: pointer;
  width: fit-content;
  font-size: 18px;
  font-weight: 600;
  line-height: 27px;
  padding: 12px 20px;
  background-color: #000;
  border-radius: 12px;
  color: #fff;
}

code {
  font-family: "Manrope", sans-serif;
}
